.nav-container{
	width: 100%;
	height: 67px;
	background-color: transparent;
	display: flex;
	justify-content: center;
	align-items: center;
}

.nav-container .nav-menu{
	width: auto;
	height: auto;
	position: relative;
}

.nav-container .nav-menu input{
	color:crimson;
}
.nav-container .nav-menu .menu-car{
	position: absolute;
	width: 350px;
	max-height: 400px;
	height:auto;
	background-color: white;
	box-shadow: 0 0 4px #ccc;
	color: rgb(75, 72, 72);
	margin: 0 auto;
	border-radius: 8px;
}
.car-hide{
	display: none;
}
.car-show{
	animation: carshow ease 400ms;
	animation-fill-mode: forwards;
}
@keyframes carshow {
	0%{
		opacity: 0;
		transform: translateY(20px);
	}
	100%{
		transform: translateY(0);
		opacity: 1;
	}
}

.menu-car .car-body{
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	overflow: hidden;
	overflow-y: scroll;
	border-radius: 8px;
	max-height: 350px ;
	height:auto;
	padding-left: 9px;
	padding-right: 9px;
	margin-bottom: 40px;
}
.menu-car .car-footer{
	color: white;
	background-color: rgb(20, 20, 20);
	width: 100%;
	position: absolute;
	bottom: 0;
	border-bottom-right-radius: 8px;
	border-bottom-left-radius: 8px;
}
.menu-car .car-body .card-car{
	display: flex;
	justify-content: flex-start;
	flex-direction: row;
}

.menu-car .car-body .card-car button{
	display: block;
	width: 10px;
	height: 10px;
	background-color: transparent;
	color: red;
	font-weight: bolder;
	font-size: larger;
	padding: 10px;
	border: none;
	flex-basis: 1%;
	margin-right: 10px;
}
.menu-car .car-body .card-car .p1{
	flex-basis: 70%;
}
.menu-car .car-body .card-car .p2{
	flex-basis: 29%;
	align-self: center;
	margin-left: 10px;
}


/* _________Forma de pago__________  */
.form-pay{
	display: flex;
	justify-content: center;
	flex-direction: column;
	width: 70%;
	height: 400px;
	margin: 0 auto;
	margin-bottom: 50px;
	margin-top: 50px;
	padding: 20px;
	box-shadow: 0 0 8px #ccc;
}
.form-pay input{
	margin-bottom: 20px;
	display: block;
	width: 98%;
	height: 40px;
}
.form-pay button{
	align-self: flex-end;
	display: block;
	width: 100%;
	height: 40px;
	margin-bottom: 20px;
	background-color: rgb(0, 106, 206);
	border: none;
	color: white;
	font-size: 1.2em;
	font-weight: bold;
	border-radius: 8px;
	padding: 10px;
	transition: all 400ms;
}
.form-pay button:hover{
	background-color: blue;
}

@media screen and (max-width:550px){
	.nav-container .nav-menu .menu-car{
		width: 250px;
	}
	.form-pay{
		width: 200px;
	}
}