.nav-container{
	width: 100%;
	height: 67px;
	background-color: transparent;
	display: flex;
	justify-content: center;
	align-items: center;
}

.nav-container .nav-menu{
	width: auto;
	height: auto;
	position: relative;
}

.nav-container .nav-menu input{
	color:crimson;
}
.nav-container .nav-menu .menu-car{
	position: absolute;
	width: 350px;
	max-height: 400px;
	height:auto;
	background-color: white;
	box-shadow: 0 0 4px #ccc;
	color: rgb(75, 72, 72);
	margin: 0 auto;
	border-radius: 8px;
}
.car-hide{
	display: none;
}
.car-show{
	-webkit-animation: carshow ease 400ms;
	        animation: carshow ease 400ms;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}
@-webkit-keyframes carshow {
	0%{
		opacity: 0;
		transform: translateY(20px);
	}
	100%{
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes carshow {
	0%{
		opacity: 0;
		transform: translateY(20px);
	}
	100%{
		transform: translateY(0);
		opacity: 1;
	}
}

.menu-car .car-body{
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	overflow: hidden;
	overflow-y: scroll;
	border-radius: 8px;
	max-height: 350px ;
	height:auto;
	padding-left: 9px;
	padding-right: 9px;
	margin-bottom: 40px;
}
.menu-car .car-footer{
	color: white;
	background-color: rgb(20, 20, 20);
	width: 100%;
	position: absolute;
	bottom: 0;
	border-bottom-right-radius: 8px;
	border-bottom-left-radius: 8px;
}
.menu-car .car-body .card-car{
	display: flex;
	justify-content: flex-start;
	flex-direction: row;
}

.menu-car .car-body .card-car button{
	display: block;
	width: 10px;
	height: 10px;
	background-color: transparent;
	color: red;
	font-weight: bolder;
	font-size: larger;
	padding: 10px;
	border: none;
	flex-basis: 1%;
	margin-right: 10px;
}
.menu-car .car-body .card-car .p1{
	flex-basis: 70%;
}
.menu-car .car-body .card-car .p2{
	flex-basis: 29%;
	align-self: center;
	margin-left: 10px;
}


/* _________Forma de pago__________  */
.form-pay{
	display: flex;
	justify-content: center;
	flex-direction: column;
	width: 70%;
	height: 400px;
	margin: 0 auto;
	margin-bottom: 50px;
	margin-top: 50px;
	padding: 20px;
	box-shadow: 0 0 8px #ccc;
}
.form-pay input{
	margin-bottom: 20px;
	display: block;
	width: 98%;
	height: 40px;
}
.form-pay button{
	align-self: flex-end;
	display: block;
	width: 100%;
	height: 40px;
	margin-bottom: 20px;
	background-color: rgb(0, 106, 206);
	border: none;
	color: white;
	font-size: 1.2em;
	font-weight: bold;
	border-radius: 8px;
	padding: 10px;
	transition: all 400ms;
}
.form-pay button:hover{
	background-color: blue;
}

@media screen and (max-width:550px){
	.nav-container .nav-menu .menu-car{
		width: 250px;
	}
	.form-pay{
		width: 200px;
	}
}
.search-container{
	background-color: #002140;
	width: 100%;
	height: 25vh;
	border-top: 2px solid rgb(18, 18, 71);
	border-bottom: 1px solid rgb(18, 18, 71);
	display: flex;
	justify-content: center;
	align-items: center;
}
.search-container .search-brand{
	width: 200px;
	background-color: transparent;
	height: 15vh;
	font-family: 'Permanent Marker', cursive;
	color: white;
	font-size: 3em;
	font-weight: normal;
	display: flex;
	line-height: 15vh;
	justify-content: space-around;
}
.search-container .search-brand:active{
	text-decoration: none;
	outline: none;
	border-style: none;
}
.search-container .search-brand img{
	width: 100%;
	height: 100%;
}
.search-container .search-form{
	width: 55%;
	height: 15vh;
	background-color: transparent;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	padding-bottom: 15px;
}
.search-container .search-form .input-container{
	display: flex;
	align-items: center;
	border-top-left-radius: 30px;
	border-bottom-left-radius: 30px;
	border-top-right-radius: 30px;
	border-bottom-right-radius: 30px;
	min-width: 50px;
	width: 700px;
	box-shadow: 0 0 4px #ccc;
	margin-left: 50px;
}
.search-container .search-form .input-container input{
	min-width: 50px;
	display: block;
	width: 100%;
	height: 48px;
	font-size: 20px;
	color: rgb(94, 91, 91);
	background-color: white;
	border-top-left-radius: 30px;
	border-bottom-left-radius: 30px;
	padding-left: 5%;
	overflow: hidden;
	border: none;
}

.search-container .search-form .input-container button{
	display: block;
	width: 80px;
	height: 48px;
	font-size: 20px;
	background-color: white;
	border-top-right-radius: 30px;
	border-bottom-right-radius: 30px;
	cursor: pointer;
	color: #002140;
	border: none;
	cursor: pointer;
}

@media screen and  (max-width: 600px){
	.search-container{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-flow: column;
		padding-top: 67px;
	}
	.search-container .search-form{
		width: 90%;
	}
	.search-container .search-form .input-container{
		width: 100%;
		margin: 0 auto;
	}
	.search-container .search-form .input-container input{
		margin-left: 0;
		width: 100%;
	}
}

.footer-container{
	width: 100%;
	height: 135px;
	background-color: #000000;
	color: white;
	font-family: monospace;
	display: flex;
	justify-content: center;
	padding-top: 25px;
	padding-bottom: 5px;
}
.footer-container .footer-social,
.footer-container .footer-contact,
.footer-container .footer-subscribe{
	width: auto;
	margin: 0px 4px;
	padding: 10px;
}
.footer-container .footer-contact,
.footer-container .footer-subscribe{
	display: flex;
	flex-direction: column;
}
.book-detail-container{
	width: 95%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 40px;
}
.img-container{
	border-radius: 8px;
	height: 400px;
	width: 260px;
	padding: 10px;
}
.book-detail-container .price-container{
	width: 100%;
	margin: 0 auto;
}
.book-detail-container .img-container img{
	width: 100%;
	height: 100%;
	border-radius: 8px;
	padding: 5px;
}
.book-detail-container .btn-container{
	width: 270px;
	margin-bottom: 40px;
}
.book-detail-container p{
	font-size: 1.8em;
	color: #002140;
}
.book-detail-container .info-container p{
	font-size: 16px;
	color: gray;
	padding: 12px;
}
.book-detail-container .author{
	color: rgb(65, 60, 60);
	font-family: Georgia, 'Times New Roman', Times, serif;
}

#header{
	position: relative;
	width: 100%;
	height: 75vh;
	overflow: hidden;
	background-color: black;
}
.header-container{
	width: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 100% 100%;
	color: white;
	height: 100%;
	padding-top: 15px;
	position: absolute;
	top: 0;
	z-index: 99;
	box-shadow: inset 0 0 8px rgb(0, 0, 0);
}
.arrow-left{
	position: absolute;
	z-index: 200;
	top: 50%;
	background-color:transparent;
	border: none;
	color: white;
	border-radius: 100%;
	left: 0;
	cursor: pointer;
}
.arrow-right{
	position: absolute;
	z-index: 200;
	top: 50%;
	background-color:transparent;
	border: none;
	color: white;
	border-radius: 100%;
	right: 0	;
	cursor: pointer;
}
.header-container .card{
	background-color: transparent;
	color: white;
	height: 58vh;
	width: 80%;
	position: absolute;
	bottom: 50px;
	left: 90px;
	display: flex;
}
.header-container .card .card-imagen{
	width: 350px;
	height: 460px;
	border-radius: 8px;
}
.header-container .card .card-imagen img{
	border-radius: inherit;
	width: 100%;
	height: 100%;
}
.header-container .card .card-body{
	max-width: 700px;
	margin-left: 70px;
	position: relative;
	display: block;
	width: 700px;
	height: auto;
}
.header-container .card .card-body .card-text{
	width: 100%;
	background-color: rgba(0, 0, 0, 0.425);
	border-radius: 8px;
	padding: 10px 17px;
	position: absolute;
	bottom: 0px;
	left: 0px;
}
.header-container .card .card-body .card-text p{
	color: #f8f8ff;
	margin-bottom: 20px;
}
.slideShow{
	-webkit-animation: slideshow linear 800ms;
	        animation: slideshow linear 800ms;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}
.slideHide{
	-webkit-animation: slidehide linear 800ms;
	        animation: slidehide linear 800ms;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes slidehide{
	0%{
		transform: translateX(0);
	}
	100%{
		transform: translateX(-100%);
	}
}

@keyframes slidehide{
	0%{
		transform: translateX(0);
	}
	100%{
		transform: translateX(-100%);
	}
}
@-webkit-keyframes slideshow{
	0%{
		transform: translateX(+100%);
	}
	100%{
		transform: translateX(0);
	}
}
@keyframes slideshow{
	0%{
		transform: translateX(+100%);
	}
	100%{
		transform: translateX(0);
	}
}
.section-container{
	position: relative;
	z-index: 98;
	padding-top: 50px;
	padding-bottom: 50px;
	width: 80%;
	margin: 0 auto;
}

.section-container .card{
	min-width: 150px;
	max-width: 200px;
	width: 180px;
	max-height: 310px;
	height: 100%;
	margin: 30px auto;
	border-radius: 8px;
	position: relative;
	transition: all ease 300ms;
	margin-top: 20px;
	overflow: hidden;
	box-shadow: 0px 0px 3px #ccc;

}
.section-container .card:hover{
	box-shadow: 0px 0px 4px rgb(0, 0, 0);
}

.hide{
	visibility: hidden;
}
.section-container .card .card-rate-price{
	width: 100%;
	max-height: 10%;
	height: 10%;
	border-top-left-radius: 8px;
	border-top-right-radius: 8px;
	background-color: rgba(0, 0, 0, 0.6);
	color: white;
	position: absolute;
	top: 0px;
	display: flex;
	justify-content: space-around;
	padding-top:15px;
	z-index: 99;
	font-size: 16;
	font-weight: bold;
	line-height: 0;
}
.section-container .card:hover .animated-show{
	-webkit-animation: show ease-in-out 400ms;
	        animation: show ease-in-out 400ms;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}
@-webkit-keyframes show{
	0%{
		visibility: visible;
		transform: translateY(-40%);
		opacity: 0;
	}
	100%{
		visibility: visible;
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes show{
	0%{
		visibility: visible;
		transform: translateY(-40%);
		opacity: 0;
	}
	100%{
		visibility: visible;
		transform: translateY(0);
		opacity: 1;
	}
}
.section-container .card .card-body{
	height: 100%;
	width: 100%;
	border-radius: 8px;
}
.section-container .card .card-body img{
	width: 100%;
	height: 100%;
	border-radius: 8px;
}
.section-container .card .card-footer{
	width: 100%;
	max-height: 10%;
	height: 10%;
	position: absolute;
	bottom: 0px;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;
	background-color: rgba(0, 0, 0, 0.6);
	display: flex;
	justify-content: baseline;
	z-index: 99;
	padding: 15px 0px;
}

.section-container .card:hover .animated-show2{
	-webkit-animation: show2 ease-in-out 400ms;
	        animation: show2 ease-in-out 400ms;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}
@-webkit-keyframes show2{
	0%{
		visibility: visible;
		transform: translateY(125%);
		opacity: 0;
	}
	100%{
		visibility: visible;
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes show2{
	0%{
		visibility: visible;
		transform: translateY(125%);
		opacity: 0;
	}
	100%{
		visibility: visible;
		transform: translateY(0);
		opacity: 1;
	}
}
