.section-container .card{
	min-width: 150px;
	max-width: 200px;
	width: 180px;
	max-height: 310px;
	height: 100%;
	margin: 30px auto;
	border-radius: 8px;
	position: relative;
	transition: all ease 300ms;
	margin-top: 20px;
	overflow: hidden;
	box-shadow: 0px 0px 3px #ccc;

}
.section-container .card:hover{
	box-shadow: 0px 0px 4px rgb(0, 0, 0);
}

.hide{
	visibility: hidden;
}
.section-container .card .card-rate-price{
	width: 100%;
	max-height: 10%;
	height: 10%;
	border-top-left-radius: 8px;
	border-top-right-radius: 8px;
	background-color: rgba(0, 0, 0, 0.6);
	color: white;
	position: absolute;
	top: 0px;
	display: flex;
	justify-content: space-around;
	padding-top:15px;
	z-index: 99;
	font-size: 16;
	font-weight: bold;
	line-height: 0;
}
.section-container .card:hover .animated-show{
	animation: show ease-in-out 400ms;
	animation-fill-mode: forwards;
}
@keyframes show{
	0%{
		visibility: visible;
		transform: translateY(-40%);
		opacity: 0;
	}
	100%{
		visibility: visible;
		transform: translateY(0);
		opacity: 1;
	}
}
.section-container .card .card-body{
	height: 100%;
	width: 100%;
	border-radius: 8px;
}
.section-container .card .card-body img{
	width: 100%;
	height: 100%;
	border-radius: 8px;
}
.section-container .card .card-footer{
	width: 100%;
	max-height: 10%;
	height: 10%;
	position: absolute;
	bottom: 0px;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;
	background-color: rgba(0, 0, 0, 0.6);
	display: flex;
	justify-content: baseline;
	z-index: 99;
	padding: 15px 0px;
}

.section-container .card:hover .animated-show2{
	animation: show2 ease-in-out 400ms;
	animation-fill-mode: forwards;
}
@keyframes show2{
	0%{
		visibility: visible;
		transform: translateY(125%);
		opacity: 0;
	}
	100%{
		visibility: visible;
		transform: translateY(0);
		opacity: 1;
	}
}