.footer-container{
	width: 100%;
	height: 135px;
	background-color: #000000;
	color: white;
	font-family: monospace;
	display: flex;
	justify-content: center;
	padding-top: 25px;
	padding-bottom: 5px;
}
.footer-container .footer-social,
.footer-container .footer-contact,
.footer-container .footer-subscribe{
	width: auto;
	margin: 0px 4px;
	padding: 10px;
}
.footer-container .footer-contact,
.footer-container .footer-subscribe{
	display: flex;
	flex-direction: column;
}