.book-detail-container{
	width: 95%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 40px;
}
.img-container{
	border-radius: 8px;
	height: 400px;
	width: 260px;
	padding: 10px;
}
.book-detail-container .price-container{
	width: 100%;
	margin: 0 auto;
}
.book-detail-container .img-container img{
	width: 100%;
	height: 100%;
	border-radius: 8px;
	padding: 5px;
}
.book-detail-container .btn-container{
	width: 270px;
	margin-bottom: 40px;
}
.book-detail-container p{
	font-size: 1.8em;
	color: #002140;
}
.book-detail-container .info-container p{
	font-size: 16px;
	color: gray;
	padding: 12px;
}
.book-detail-container .author{
	color: rgb(65, 60, 60);
	font-family: Georgia, 'Times New Roman', Times, serif;
}
