@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');
.search-container{
	background-color: #002140;
	width: 100%;
	height: 25vh;
	border-top: 2px solid rgb(18, 18, 71);
	border-bottom: 1px solid rgb(18, 18, 71);
	display: flex;
	justify-content: center;
	align-items: center;
}
.search-container .search-brand{
	width: 200px;
	background-color: transparent;
	height: 15vh;
	font-family: 'Permanent Marker', cursive;
	color: white;
	font-size: 3em;
	font-weight: normal;
	display: flex;
	line-height: 15vh;
	justify-content: space-around;
}
.search-container .search-brand:active{
	text-decoration: none;
	outline: none;
	border-style: none;
}
.search-container .search-brand img{
	width: 100%;
	height: 100%;
}
.search-container .search-form{
	width: 55%;
	height: 15vh;
	background-color: transparent;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	padding-bottom: 15px;
}
.search-container .search-form .input-container{
	display: flex;
	align-items: center;
	border-top-left-radius: 30px;
	border-bottom-left-radius: 30px;
	border-top-right-radius: 30px;
	border-bottom-right-radius: 30px;
	min-width: 50px;
	width: 700px;
	box-shadow: 0 0 4px #ccc;
	margin-left: 50px;
}
.search-container .search-form .input-container input{
	min-width: 50px;
	display: block;
	width: 100%;
	height: 48px;
	font-size: 20px;
	color: rgb(94, 91, 91);
	background-color: white;
	border-top-left-radius: 30px;
	border-bottom-left-radius: 30px;
	padding-left: 5%;
	overflow: hidden;
	border: none;
}

.search-container .search-form .input-container button{
	display: block;
	width: 80px;
	height: 48px;
	font-size: 20px;
	background-color: white;
	border-top-right-radius: 30px;
	border-bottom-right-radius: 30px;
	cursor: pointer;
	color: #002140;
	border: none;
	cursor: pointer;
}

@media screen and  (max-width: 600px){
	.search-container{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-flow: column;
		padding-top: 67px;
	}
	.search-container .search-form{
		width: 90%;
	}
	.search-container .search-form .input-container{
		width: 100%;
		margin: 0 auto;
	}
	.search-container .search-form .input-container input{
		margin-left: 0;
		width: 100%;
	}
}
