#header{
	position: relative;
	width: 100%;
	height: 75vh;
	overflow: hidden;
	background-color: black;
}
.header-container{
	width: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 100% 100%;
	color: white;
	height: 100%;
	padding-top: 15px;
	position: absolute;
	top: 0;
	z-index: 99;
	box-shadow: inset 0 0 8px rgb(0, 0, 0);
}
.arrow-left{
	position: absolute;
	z-index: 200;
	top: 50%;
	background-color:transparent;
	border: none;
	color: white;
	border-radius: 100%;
	left: 0;
	cursor: pointer;
}
.arrow-right{
	position: absolute;
	z-index: 200;
	top: 50%;
	background-color:transparent;
	border: none;
	color: white;
	border-radius: 100%;
	right: 0	;
	cursor: pointer;
}
.header-container .card{
	background-color: transparent;
	color: white;
	height: 58vh;
	width: 80%;
	position: absolute;
	bottom: 50px;
	left: 90px;
	display: flex;
}
.header-container .card .card-imagen{
	width: 350px;
	height: 460px;
	border-radius: 8px;
}
.header-container .card .card-imagen img{
	border-radius: inherit;
	width: 100%;
	height: 100%;
}
.header-container .card .card-body{
	max-width: 700px;
	margin-left: 70px;
	position: relative;
	display: block;
	width: 700px;
	height: auto;
}
.header-container .card .card-body .card-text{
	width: 100%;
	background-color: rgba(0, 0, 0, 0.425);
	border-radius: 8px;
	padding: 10px 17px;
	position: absolute;
	bottom: 0px;
	left: 0px;
}
.header-container .card .card-body .card-text p{
	color: #f8f8ff;
	margin-bottom: 20px;
}
.slideShow{
	animation: slideshow linear 800ms;
	animation-fill-mode: forwards;
}
.slideHide{
	animation: slidehide linear 800ms;
	animation-fill-mode: forwards;
}

@keyframes slidehide{
	0%{
		transform: translateX(0);
	}
	100%{
		transform: translateX(-100%);
	}
}
@keyframes slideshow{
	0%{
		transform: translateX(+100%);
	}
	100%{
		transform: translateX(0);
	}
}